import React, { Component } from 'react'
import {Button, Container, Row, Col } from 'reactstrap';
import '../styles/Section.css';

export default class Section extends Component {
    render() {
        return (
            <section className='bg'>
                <Container>
                    <Row className='align-items-center'>
                        <Col className='col-12 d-md-8 col-lg-6 hero-text'>
                            <h1>Find if Doctor's are on leave or not.</h1>
                            <p>Get the list of doctors who are on duty today and their respective specialization in any hospital in <b>Calicut</b></p>
                            <Button className='btn-primary' href="#">Get the List</Button>
                        </Col>
                    </Row>
                </Container>
            </section>
        )
    }
}
